/* theme */
.lbtn.lbtn-telegram {
    color: #ffffff;
    background-color: #37AEE2;
    border-color: #4da5cb;
}
.lbtn.lbtn-telegram.white {
    color: #37AEE2;
    background-color: #ffffff;
    border-color: #d7d7d7;
}

/* logo */
.lbtn.lbtn-telegram .logo {
    background: url(9c55690bd7068f705d8241ee26af2f74.svg);
}
.lbtn.lbtn-telegram:not(.white) .logo {
    filter: none;
}